
import { defineComponent, onMounted, watch } from 'vue';
import Layout from "@/apps/HTS/views/reports/templates/Layout.vue";
import { HtsReportComposable } from "@/apps/HTS/composables/HtsReportComposable";
import { isEmpty } from 'lodash';
import { HtsReportService } from "@/apps/HTS/services/hts_report_service"
import { toCsv, toTablePDF } from "@/utils/Export";

export default defineComponent({
    components: {
        Layout
    },
    props: {
        params: {
            type: Object,
            required: true
        }
    },
    setup(props) {
        const report = HtsReportComposable(props.params.title)
        function configureReport() {
            report.setReportPeriod({
                isPeriodSelection: true,
                minDate: props.params.minDate,
                maxDate: props.params.maxDate,
                generate: (_: any, f: Record<string, any>, computedData: Record<string, any>) => {
                    const report = new HtsReportService(props.params.endpoint)
                    report.startDate = computedData.start_date
                    report.endDate = computedData.end_date
                    report.name = props.params.name
                    return report.requestReport({ type: 'moh' })
                }
            })
        }

        function onClickValue(indicator: any) {
            console.log(indicator.value)
            if (!isEmpty(indicator._drill)) {
                report.defaultDrillDown(indicator.label, indicator._drill)
            }
        }
        
        function createExportData() {
            const [_, ...rows] = report.reportData.indicators.map((indicator: any) => {
                return [
                    indicator.index || '', indicator.label, indicator._value || ''
                ]
            })
            return {
                rows,
                columns: [['#', report.reportData.indicators[0].label, '#']]
            }
        }

        watch(() => report.reportData.data, (data) => {
            report.reportData.indicators = props.params.indicators.map((indicator: any) => {
                if (typeof indicator.value === 'function') {
                    const value = indicator.value(data)
                    if (Array.isArray(value)) {
                        indicator._value = value.length
                        indicator._drill = value
                    }
                }
                return indicator
            })
        }, { deep: true })

        onMounted(() => {
            if (isEmpty(report.reportData.data)) {
                report.reportData.indicators = props.params.indicators.map((indicator: any) => {
                    if (typeof indicator.value === 'function') {
                        indicator._value = ''
                    }
                    return indicator
                })
                configureReport()
            }
        })

        return {
            onClickValue,
            configureReport,    
            logo: 'login-logos/Malawi-Coat_of_arms_of_arms.png',
            title: report.title,
            period: report.period,
            reportData: report.reportData,
            dateGenerated: report.dateGenerated,
            refresh: report.updateReportData,
            toPDF: () => {
                const { rows, columns } = createExportData()
                toTablePDF(
                    columns,
                    rows,
                    report.reportDescription()
                )
            },
            toCSV: () => {
                const { rows, columns } = createExportData()
                toCsv(
                    columns,
                    report.appendReportStamp(rows),
                    report.reportDescription()
               )
            }
        }
    }
})
